export default {
  namespaced: true,
  state: {
    module: [
      {
        id: "1",
        text: "热网管理",
        iconCls: "icon-wang",
        children: [
          {
            id: "1-6",
            text: "系统管理",
            state: "open",
            admin: 1,
            iconCls: "iconfont icon-xitongpeizhi",
            children: [
              {
                id: "1-6-1",
                path: "User",
                text: "用户管理"
              },
              {
                id: "1-6-3",
                path: "Stage",
                text: "阶段管理"
              },
              {
                id: "1-6-2",
                path: "Project",
                text: "项目管理"
              },
              {
                id: "1-6-4",
                path: "Dict",
                text: "数据字典"
              }
            ]
          },
          {
            id: "1-7",
            text: "个人管理",
            state: "open",
            admin: 0,
            iconCls: "iconfont icon-xitongpeizhi",
            children: [
              {
                id: "1-7-1",
                path: "MyProject",
                text: "我的项目"
              }
            ]
          }
        ]
      }
    ],
    selectModule: "1",
    selectModuleTitle: "",
    sliderMenu: [],
    sliderActive: {},
    sliderExpand: false,
    tabs: [
      {
        path: "/home",
        text: "首页"
      }
    ],
    tabIndex: 0,
    isActiveRouter: true
  },
  mutations: {
    set_module(state, payload) {
      state.selectModule = payload;
      const target = state.module.find(m => m.id == state.selectModule);
      state.sliderMenu = [];
      setTimeout(() => {
        state.sliderMenu = target.children;
        state.selectModuleTitle = target.text;
      }, 1);
    },
    set_slider_active(state, payload) {
      state.sliderActive = payload;
    },
    set_mode(state, payload) {
      state.mode = payload;
    },
    set_slider_expand(state, payload) {
      state.sliderExpand = payload;
    },
    add_tab(state, payload) {
      const index = state.tabs.findIndex(t => t.text == payload.text);
      if (index == -1) {
        state.tabs.push(payload);
        state.tabIndex = state.tabs.length - 1;
      } else {
        state.tabIndex = index;
      }
    },
    remove_tab(state, payload) {
      state.tabs = state.tabs.filter(t => t.text != payload.text);
    },
    set_tab_index(state, payload) {
      state.tabIndex = payload;
    },
    init() {
      this.commit("app/set_module", 1);
    }
  },
  actions: {
    refresh_router({ state }) {
      state.isActiveRouter = false;
      setTimeout(() => {
        state.isActiveRouter = true;
      }, 100);
    }
  }
};
